/*
=====================================
Footer
=====================================
*/

#footer {
  text-align: center;
  background: $color-gray-200;
  padding: 3rem 0;

  .social-media-footer {
    display: flex;
    justify-content: center;
    margin-bottom: 1rem;

    a {
      width: 3rem;
      height: 3rem;
      font-size: 1rem;
      display: flex;
      justify-content: center;
      align-items: center;
      color: $color-dark;
      background: $color-gray-300;
      border-radius: 50%;
      margin: 0.25rem;

      &:hover {
        background: $color-gray-400;
      }
    }
  }

  .copyright {
    font-size: 0.9rem;
    color: $color-gray-700;

    a {
      color: $color-gray-800;
    }
  }
}